import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, InputNumber, Checkbox } from 'antd';
import { getGradePartById, createGradePart, updateGradePart } from 'services/typeComponentPoint';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formzone' })
class FormZonePage extends Component {
  state = {
    pointTypeId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ pointTypeId: match.params.id });
      this.getGradePartById(match.params.id);
    }
  };

  getGradePartById = async id => {
    const res = await getGradePartById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async(err, values) => {
      if (!err) {
        const { pointTypeId } = this.state;
        try {
          if (!pointTypeId) {
            await createGradePart(values);
          } else {
            await updateGradePart({ id: pointTypeId, ...values });
          }
          this.props.history.goBack();
        } catch (error) {
          console.log(error);
        }
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { pointTypeId } = this.state;
    console.log(this.props);
    return (
      <Card title={`${pointTypeId ? 'Cập nhật' : 'Thêm'} khu vực`}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Ký hiệu" {...formItemLayout}>
              {getFieldDecorator('code', {
                initialValue: initialValue.code,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                  {
                    max: 5,
                    message: 'Tối đa 5 ký tự',
                  }
                ],
              })(<Input disabled={pointTypeId !== false} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tỷ lệ/Hệ số" hasFeedback>
              {getFieldDecorator('factor', {
                initialValue: initialValue.factor,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<InputNumber />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tỷ lệ nhóm" hasFeedback>
              {getFieldDecorator('ratio', {
                initialValue: initialValue.ratio,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<InputNumber />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Nhóm thành phần" hasFeedback>
              {getFieldDecorator('partGroup', {
                initialValue: initialValue.partGroup,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<InputNumber />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Chuyên cần">
              {getFieldDecorator('isAttendant', {
                valuePropName: 'checked',
                initialValue: initialValue.isAttendant,
              })(<Checkbox />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Chọn mặc định">
              {getFieldDecorator('isDefault', {
                valuePropName: 'checked',
                initialValue: initialValue.isDefault,
              })(<Checkbox />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Thực hành">
              {getFieldDecorator('isExcercise', {
                valuePropName: 'checked',
                initialValue: initialValue.isExcercise,
              })(<Checkbox />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormZonePage {...props} />
  </Page>
));



