import { request } from 'utils';

const prefix = '/api/categories';


export const getGradeParts = ({
  code,
  label,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/GradePart',
    method: 'GET',
    params: {
      code,
      label,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });


export const createGradePart = ({
  code,
  label,
  factor,
  ratio,
  partGroup,
  isAttendant,
  isDefault,
  isExcercise
} = {}) =>
  request({
    prefix,
    url: '/GradePart',
    method: 'POST',
    params: {},
    data: {
      code,
      label,
      factor,
      ratio,
      partGroup,
      isAttendant,
      isDefault,
      isExcercise
    }
  });


export const getGradePartById = id =>
  request({
    prefix,
    url: `/GradePart/${id}`,
    method: 'GET',
    params: {},
    data: {}
  });


export const updateGradePart = ({
  id,
  code,
  label,
  factor,
  ratio,
  partGroup,
  isAttendant,
  isDefault,
  isExcercise
} = {}) =>
  request({
    prefix,
    url: `/GradePart/${id}`,
    method: 'PUT',
    params: {},
    data: {
      id,
      code,
      label,
      factor,
      ratio,
      partGroup,
      isAttendant,
      isDefault,
      isExcercise
    }
  });



export const deleteGradePart = id =>
  request({
    prefix,
    url: `/GradePart/${id}`,
    method: 'DELETE',
    params: {},
    data: {}
  });



